import axios from 'axios'

// 封装post请求
export const post = (url, data) => {
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
  })
}

// 封装post请求
export const get = (url, data) => {
  return new Promise((resolve, reject) => {
    axios.get(url, data)
      .then(response => {
        if (response.data.code === 0) {
          resolve(response.data)
        }
      }, err => {
        reject(err)
      })
  })
}



