import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: () => import('@/views/login/index.vue')
    },
    {
      path: '/home',
      name: '首页',
      title: '首页',
      component: () => import('@/views/content/index.vue'),
      children: [{
        name: '主页面',
        path: '/',
        component: () => import('@/views/content/customer/newcustomer/index.vue')
      }]
    }
  ]
})



